import React from 'react'
import './index.css'
import {NavLink} from 'react-router-dom'
export default function Nav() {
    return (
        <div className="nav">
        <ul>
            <li> <NavLink to='/home' activeClassName='navActive'><span className="icon-gaishu iconfont nav-icon"></span>首页</NavLink></li>
            <li> <NavLink to='/essay' activeClassName='navActive'><span className="icon-tubiaozhizuomoban- iconfont nav-icon"></span>随笔心得</NavLink></li>
            <li> <NavLink to='/tecnology' activeClassName='navActive'><span className="icon-gouwuche iconfont nav-icon"></span>技术分享</NavLink></li>
            <li> <NavLink to='/tool' activeClassName='navActive'><span className="icon-gerenzhongxin iconfont nav-icon"></span>功能栏</NavLink></li>
            <li> <NavLink to='/message' activeClassName='navActive'><span className="icon-guanyuwomen iconfont nav-icon"></span>留言板</NavLink></li>
        </ul>
    </div>
    )
}
